.wrapper {
    height: 100vh;
    display: none;
}

.wrapper ul li {
    width: 40vw;
    height: 37vh;
    background-color: red;
}

.wrapper ul {
    height: 100vh;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 1%;
    background-color: antiquewhite;
}